import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import { Input } from '@vkontakte/vkui';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import cloud from '../img/cloud.png';
import './StyleRegisterMenu.css';

const osName = platform();

const RegisterMenu = (props) => (
	<Panel id={props.id}>
		<PanelHeader>
			<Div className = "PanelHeaderRegisterMenu">
				Регистрация
			</Div>
		</PanelHeader>
		<Div className="MainRegisterMenu">
			<Div className = "TextRegisterMenuSignIn">
				Войти
			</Div>
			<Div className = "TextRegisterMenuCode">
				Регистрационный код
			</Div>
			<Div>
				<Input className = "InputRegisterMenu" type="text" placeholder= "Введите регистрационный код">
				</Input>
			</Div>
			<Div className = "ButtonDivRegisterMenu">
				<Button className = "ButtonRegisterMenu" size="xl" mode="outline" onClick={props.go} data-to="mainMenu">
					<Div className = "TextButtonRegisterMenu">ВОЙТИ</Div>
				</Button>
			</Div>
		</Div>
	</Panel>
);

RegisterMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default RegisterMenu;